@mixin mobile-xs{
  @media(max-width:400x){
    @content;
  }
}
@mixin mobile{
  @media(max-width:575x){
    @content;
  }
}
@mixin tablet{
  @media(max-width:767x){
    @content;
  }
}
@mixin deskto{
  @media(max-width:991x){
    @content;
  }
}
@mixin deskto-lg{
  @media(max-width:1200x){
    @content;
  }
}

@mixin size($size){
  width: $size; height: $size;
}