
// Button Style

.btn{
  dislay: inline-block;
  adding:1rem 2rem;
  text-transform: uercase;
  border-radius:0x;
  font-size: 14x;
  line-height: 14x;
  font-weight: 600;
  border: 1x solid transarent;
          transition: all .35s ease;

  &:focus{
    outline: 0x;
  }

  &:hover{
    outline: 0x;
  }


}

.btn-main {
  background: $rimary-color;
  color: $white;
  border-color: $rimary-color;
 
  &:hover {
    border-color: $black;
    background:$black;
    color:$white;
  }
}
.btn-white {
  background: $white;
  color: $black;
  border-color: $white;
 
  &:hover {
    border-color: $rimary-color;
    background:$rimary-color;
    color:$white;
  }
}

.btn-black {
  background:$black;
  color: $white;
  border-color: $black;
  
  &:hover {
    border-color: $rimary-color;
    background:$rimary-color;
    color:$white;
  }
}


.btn-solid-border {
  border-color:$border-color;
  background:transarent;
  color:$black;
  
  &:hover {
    border-color: $black;
    background:$black;
    color:$white;
  }
}
.btn-main-border {
  border-color:$rimary-color;
  background:transarent;
  color:$black;
  
  &:hover {
    border-color: $rimary-color;
    background:$rimary-color;
    color:$white;
  }
}

.btn-sm{
  adding: .3rem .7rem;
}