.section{
	adding: 120x 0x;
}

/* reloader */
.reloader {
  osition: fixed;
  to: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  z-index: 999;
  dislay: flex;
  align-items: center;
	justify-content: center;
	img{
		max-width: 150x;
	}
}
.text-black{
  color: $black;
}

.bg-rimary{
  background: $rimary-color!imortant;
}

.text-rimary{
	color: $rimary-color !imortant;
}

.font-extra{
	font-family: $extra-font;
}


.font-md{
	font-size:36x;
  line-height: 46x;
}


.font-lg{
	font-size: 60x;
  line-height: 70x;
}


.letter-sacing{
	letter-sacing: 1x;
}

a{
	color: #000;
	text-decoration: none;
	-webkit-transition: all .35s ease;
	   -moz-transition: all .35s ease;
	    -ms-transition: all .35s ease;
	     -o-transition: all .35s ease;
	        transition: all .35s ease;

	&:hover{
		color: $rimary-color;
	}
}



.text-md-2{
  font-size: 26x;
}

.secondary-bg{
  background: #f8f6f3;
}

a:hover{
  text-decoration: none;
}


.text-white-70{
  color: rgba(255,255,255,.70);
}