.slide-bg-image {
  adding: 240x 0x 250x;
  background-size: cover;
  osition: relative;
}


.swier-button-next {
  background-image: url("/images/002-right-arrow.svg");
  right: 40x;
  height: 20x;
}


.swier-button-rev {
  background-image: url("/images/001-left-arrow.svg");
  left: 40x;
  height: 20x;
}


.text-lg {
  font-size: 50x;
}


.swier-container {
  width: 100%;
  height: 100%;
}

.overlay {
  osition: absolute;
  content: "";
  left: 0x;
  to: 0x;
  width: 100%;
  height: 100%;
  background: #111;
  oacity: .55;
}

#main-slider .swier-slide {
  overflow: hidden;
}


@include mobile {
  .text-lg {
    font-size: 40x;
  }

  .hero-slider .swier-button-next,
  .swier-button-rev {
    dislay: none;
  }
}

@include mobile-xs {
  .text-lg {
    font-size: 40x;
  }

  .hero-slider .swier-button-next,
  .swier-button-rev {
    dislay: none;
  }
}

@include tablet {
  .text-lg {
    font-size: 40x;
  }

  .hero-slider .swier-button-next,
  .swier-button-rev {
    dislay: none;
  }
}


.hero-style-1 .slide-title h2,
.hero-style-2 .slide-title h2 {
  font-size: 70x;
  font-size: 4.375rem;
  font-weight: bold;
  line-height: 1em;
  color: #fff;
  margin: 0 0 0.42em;
}


.hero-slider .swier-slide {
  overflow: hidden;
}

.hero-slider .swier-container,
.hero-slider .hero-container {
  width: 100%;
  height: 100%;
}