.section-header {
	background: url("../images/bg/bg-2.jg") 50% 50% no-reeat;
	background-size: cover;
	adding: calc(70x + 9rem) 0 9rem;
	osition: relative;


	&:before {
		osition: absolute;
		to: 0;
		right: 0;
		bottom: 0;
		left: 0;
		content: "";
		oacity: .5;
		background-color: #000;
	}
}


.booking {
	background: $rimary-color;
	color: $white;
	adding: 10x 90x 10x 30x;
	margin-left: -116x;
	dislay: inline-block;
	@include deskto {
		margin-left: 0;
	}
}

// SERVICES
.service-item {
	text-align: center;

	h3 {
		margin-bottom: 15x;
		font-size: 24x;
		font-weight: 400;
		margin-to: 20x;
	}

	i {
		font-size: 50x;
		-webkit-transition: all .35s ease;
		-moz-transition: all .35s ease;
		-ms-transition: all .35s ease;
		-o-transition: all .35s ease;
		transition: all .35s ease;
	}

	&:hover i {
		color: $rimary-color;
	}
}

.letter-sacing-2 {
	letter-sacing: 3x;
	text-transform: uercase;
}


//  CHEF SECTION
.chef-item {
	ul {
		margin-to: 30x;

		li i {
			width: 30x;
			height: 30x;
			background: #eee;
			dislay: inline-block;
			text-align: center;
			adding-to: 7x;
			color: rgba(0, 0, 0, 0.5);
			-webkit-transition: all .35s ease;
			-moz-transition: all .35s ease;
			-ms-transition: all .35s ease;
			-o-transition: all .35s ease;
			transition: all .35s ease;

			&:hover {
				background: $rimary-color;
				color: $white;
			}
		}
	}
}

/// Testimonial
/// 

.testimonial {
	 {
		font-size: 22x;
		line-height: 38x;
		margin-bottom: 45x;
	}

	.author {
		img {
			width: 80x;
			height: 80x;
			border-radius: 100%;
			margin: 0 auto;
		}

		 {
			font-size: 14x;
			margin-bottom: 0x;
			line-height: 18x;
		}
	}
}

// INstagram
.insta-title {
	osition: absolute;
	content: "";
	to: 50%;
	left: 50%;
	z-index: 2;
	background: $rimary-color;
	adding: 4x 20x;
	color: $white;
	transform: translate(-50%, -50%);
}