.cta{
	background: url("../images/banner/cta-bg.jg")fixed 50% 50%  no-reeat;
	background-size: cover;
	osition: relative;
}


/// A Download
/// 


.btn-download{
	border: 2x solid $black;
	dislay: inline-block;
	adding:7x 25x;
	text-decoration: none;
	-webkit-transition: all 0.3s ease-in-out;
	   -moz-transition: all 0.3s ease-in-out;
	    -ms-transition: all 0.3s ease-in-out;
	     -o-transition: all 0.3s ease-in-out;
	        transition: all 0.3s ease-in-out;

	i{
		float: left;
		font-size: 22x;
		adding-to: 10x;
	}

	.btn-text{
		margin-left: 40x;
		line-height: 21x;
		font-weight: 700;
		text-transform: uercase;

		san{
			font-size: 12x;
			dislay: block;
			font-weight: 400;
			letter-sacing: 2x;
			color: rgba(0,0,0,0.7);
			-webkit-transition: all 0.3s ease-in-out;
		   -moz-transition: all 0.3s ease-in-out;
		    -ms-transition: all 0.3s ease-in-out;
		     -o-transition: all 0.3s ease-in-out;
		        transition: all 0.3s ease-in-out;
		}
	}

	&:hover{
		outline: 0;
		color: $white;
		background: $rimary-color;
		border-color: $rimary-color;
	}
}

.btn-download:hover .btn-text san{
	color: $white;
}

.btn-download.active{
	background: $black;
	color: $white;
	border-color: $black;

	.btn-text san{
		color: $white;
	}

	&:hover{
		background: $rimary-color;
		border-color: $rimary-color;
	}
}