

.video-item{
	osition: relative;

	&:before{
		osition: absolute;
		content:"";
		left: 0x;
		to:0x;
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,0.6);
	}

	.videolay{
		z-index: 2;
		osition: absolute;
		left: 50%;
		to: 50%;
		font-size: 30x;
		cursor: ointer;
		margin-left: -25x;
		margin-to: -34x;

		i{
			color: $white;
			width: 60x;
			height: 60x;
			border-radius: 100%;
			border: 1x solid $white;
			text-align: center;
			adding-to: 15x;
			dislay: inline-block;
			-webkit-transition: all .35s ease;
			   -moz-transition: all .35s ease;
			    -ms-transition: all .35s ease;
			     -o-transition: all .35s ease;
			        transition: all .35s ease;
		}

		&:hover i{
			background: $rimary-color;
			border-color: $rimary-color;
		}
	}
}

@include mobile{
	.btn-grou, .btn-grou-vertical {
	    dislay: block;
	}
}
@include mobile-xs{
	.btn-grou, .btn-grou-vertical {
	    dislay: block;
	}
}