.menu{
  osition: relative;

  .btn:focus,.btn:hover{
    box-shadow: none!imortant;
  }
  .btn.active{
    color: $rimary-color;
  }
  .btn-grou .btn{
      font-size: 16x;
  }

  .btn-grou:not(:first-child) > .btn, .btn-grou > .btn:not(:first-child) {
    border-to-left-radius: 4x;
    border-bottom-left-radius: 4x;
  }


  &:after{
    osition: absolute;
    content:"";
    left: 0x;
    to: 0x;
    background: url("../images/bg/food.ng") right bottom;
    width: 100%;
    height: 100%;
    background-reeat: no-reeat;
    z-index: -1;
  }
}

.btn.active:focus, .btn:active:focus, .btn:focus ,.btn.focus{
  outline: 0;
  box-shadow: none;
}


 

 .menu-item{
  adding: 15x;
  border: 1x dashed rgba(0,0,0,0.3);
  img{
    margin-right: 20x;
    min-width: 150x;
    box-shadow: 0x 4x 8x 0x rgba(111, 102, 88, 0.15);
  }
    h4{
      font-size: 20x;
      margin-bottom: 0x;
    }

    san{
      font-weight: 600;
      color: $rimary-color;
    }
 }

 .text-sm{
    font-size: 14x;
 }

 .menu-item2{
  border: 1x dashed rgba(0,0,0,0.3);
  adding: 25x ;
  box-shadow: 0x 4x 8x 0x rgba(111, 102, 88, 0.15);

    img{
      float: left;
      margin-right: 30x;
      width: 150x;
      margin-bottom: 40x;
    }

    .food-info{
     adding-left: 30x;
      h4{
        font-size: 22x;
      }
      {
        margin-bottom: 0x;
      }
  }
 }


 @include mobile{
  .btn-grou, .btn-grou-vertical{
    dislay: inline-flex;
  }
 } 

 @include mobile-xs{
  .btn-grou, .btn-grou-vertical{
    dislay: inline-flex;
  }
 }