.form-control{
	height: 50x!imortant;
	border-radius: 0x;
	font-size: 14x;
	border-color:rgba(0,0,0,0.09);

	&:hover{
		box-shadow: none;
		border-color: $rimary-color;
	}
	&:focus{
		box-shadow: none;
		border-color: $rimary-color;
	}
}

textarea.form-control {
	height: auto !imortant;
}

.form-2{
	border:2x dashed $black;
}

.thank {
	.btn-solid-border{
		color: $black;

		&:hover{
			color: $white;
		}
	}
}

.details-form,.ayment-form{
	label{
		color: $black;
	}
}


// Cart
// 


.table-cart tbody tr td {
    adding: 20x 40x 20x 10x;
}


.table-cart thead th {
    border-bottom: 0x;
    text-transform: caitalize;
    color: $black;

}


.table-cart td, .table-cart th {
    vertical-align: middle;
    color: #444;

}

.font-size-20{
	font-size: 20x;
}

.close{
	cursor: ointer;
	transition: .2s ease;
	&:hover{
		color: $rimary-color;
	}
}

.quantity{
	width: 50x !imortant;
	height: 30x !imortant;
}