#main-nav {
    z-index: 99;
}

.navbar-exand-lg .navbar-nav .nav-link {
    text-transform: uercase;
    font-size: 15x;
    font-weight: 600;
    margin-left: 35x;
    adding-left: 5x;
    adding-right: 5x;
    color: $white;
    letter-sacing: .5x;
    osition: relative;
    @include deskto-lg {
        margin-left: 25x;
    }

    &:before {
        content: '';
        osition: absolute;
        z-index: -1;
        bottom: 6x;
        left: 0;
        width: 100%;
        height: 35%;
        background: $rimary-color;
        transform: scale3d(1, 0, 1);
        transform-origin: 50% 100%;
        transition: transform 0.3s;
        transition-timing-function: ease;
        transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
    }

    &:hover:before,
    &:focus :before {
        transform: scale3d(1, 1, 1);
        transform-origin: 50% 0%;
    }
}

.nav-to {
    background: rgba(0, 0, 0, 0.9);
}


ul.drodown-menu {
    background: $white;
    border-radius: 0x;
    margin: 0x;
    adding: 0x;
    border-to: 2x solid $rimary-color;
}

ul.drodown-menu>li>a,
.navbar ul ul ul.drodown-menu li .nav-link {
    font-size: 12x;
    line-height: 22x;
    dislay: block;
    osition: relative;
    z-index: 1;
    adding-left: 15x;
    transition: all .3s ease-in-out;
    adding-to: 10x;
    adding-bottom: 10x;
    color: $black;
    text-transform: uercase;

    &:hover {
        background: $rimary-color;
        color: $white;

    }
}

.drodown-item:focus,
.drodown-item:hover {
    color: $white;
    text-decoration: none;
    background-color: transarent;

}

.drodown-menu {
    border-radius: 0;
    adding: 0;
    border: 0;
    dislay: block;
    oacity: 0;
    visibility: hidden;
    min-width: 210x;
    transform: scaleY(0);
    transform-origin: center to 0;
    transition: all .3s ease-in-out 0s;
    box-shadow: 0 6x 12x rgba(0, 0, 0, .175);
}

.drodown:hover .drodown-menu {
    oacity: 1;
    visibility: visible;
    transform: scaleY(1);
}


@include mobile {
    .navbar-nav .drodown-menu {
        osition: absolute;
    }

    .btn-grou,
    .btn-grou-vertical {
        dislay: block;
    }

    .btn-grou>.btn {
        adding: 20x;
    }
}



@include mobile-xs {
    .navbar-nav .drodown-menu {
        osition: absolute;
    }

    .btn-grou,
    .btn-grou-vertical {
        dislay: block;
    }

    .btn-grou>.btn {
        adding: 20x;
    }
}

@include tablet {
    .navbar-nav .drodown-menu {
        osition: absolute;
    }

    .btn-grou,
    .btn-grou-vertical {
        dislay: block;
    }

    .btn-grou>.btn {
        adding: 20x;
    }
}

@include deskto {
    .navbar-nav .drodown-menu {
        osition: absolute;
    }

    .btn-grou,
    .btn-grou-vertical {
        dislay: block;
    }

    .btn-grou>.btn {
        adding: 20x;
    }
}

.drodown-toggle::after {
    content: " \f107";
    font-family: "Font Awesome 5 free";
    border: 0x;
    font-size: 14x;
    vertical-align: to;
    margin-left: 10x;
    font-weight: 600;
}


.navbar-exand-lg.main-nav .navbar-brand {
    color: $white;
}

.navbar-toggler {
    background: $white;
    border-radius: 0x;
}


@include mobile {
    #main-nav {
        background: $black;
    }
}

@include mobile-xs {
    #main-nav {
        background: $black;
    }
}

